require('./jquery.md5');
window.AJAX_PROXY = {
		project : '/artifact',
		ajaxStack : new Array(),// 
		ajaxMap : new Object()// 
};
module.exports = function AjaxProxy(options) {
	var dfd = $.Deferred(); // Deferred
	this.defaults = {
		url : '',
		type : 'POST',
		async : true,
		cache : false,
		contentType : 'application/json; charset=UTF-8',
		dataType : 'json',
		timeout : 20000,
		data : null,
		$loadingContainer : $('.layout-message')
	};
	this.options = $.extend({}, this.defaults, options);
	var obj = this;
	function calMd5(options) {
		var temp = {
			url : options.url,
			type : options.type,
			data : options.data
		};
		return $.md5(JSON.stringify(temp));
	}
	this.key = calMd5(this.options);
	var $loadingContainer = obj.options.$loadingContainer;
	require("./util");
	var uuid = $loadingContainer.getUID('loading');
	this.options.url = AJAX_PROXY.project + this.options.url;
	this.options.xhr = function() {
		/* XMLHttpRequest */
		var xhr = null;
		if (window.ActiveXObject) {
			xhr = new ActiveXObject("Microsoft.XMLHTTP");
		} else if (window.XMLHttpRequest) {
			xhr = new XMLHttpRequest();
		}
		require("./jquery.loading");
		$loadingContainer.Loading(uuid);// Loading
		var percent = 21;
		var intervalId = null;
		var timeoutId1 = null;
		var timeoutId2 = null;
		xhr.onreadystatechange = function() {
			switch (xhr.readyState) {
				case 0 : { // ()(XMLHttpRequest)open()
					$loadingContainer.progress(uuid, "0%");
					break;
				}
				case 1 : { // send()
					$loadingContainer.progress(uuid, "20%");
					intervalId = setInterval(function() {
						$loadingContainer.progress(uuid, percent + "%");
						percent = percent + 1;
					}, parseInt(obj.options.timeout / 60));
					timeoutId1 = setTimeout(function() {
						$loadingContainer.warning(uuid);
					}, parseInt(obj.options.timeout * 0.8));
					timeoutId2 = setTimeout(function() {
						window.clearInterval(intervalId);
					}, obj.options.timeout);
					break;
				}
				case 2 : { // send()
					window.clearInterval(intervalId);
					window.clearTimeout(timeoutId1);
					window.clearTimeout(timeoutId2);
					$loadingContainer.progress(uuid, "80%");
					break;
				}
				case 3 : { // 
					$loadingContainer.progress(uuid, "90%");
					break;
				}
				case 4 : { // 
					$loadingContainer.progress(uuid, "100%");
					break;
				}
				default :
					break;
			}
		};
		return xhr;
	};
	this.options.complete = function(xhr, text) {
		setTimeout(function() {
			delete AJAX_PROXY.ajaxMap[obj.key];
			$loadingContainer.removeLoading(uuid);
		}, 600);
	};
	this.options.success = function(response) {
		$loadingContainer.success(uuid);
		switch (response.code) {
			case -1 : {// 
				require('./message').danger(response.message);
				break;
			}
			case -22 : {// 
				AJAX_PROXY.ajaxStack.push(obj);
				require('./message').danger(response.message + '<a href="#" class="alert-link margin-left-15" onclick="Login();"></a>');
				break;
			}
			case -30 : {// Session
				AJAX_PROXY.ajaxStack.push(obj);
				require('./message').danger(response.message + '<a href="#" class="alert-link margin-left-15" onclick="Login();"></a>');
				break;
			}
			case -31 : {// Session
				require('./message').danger(response.message + '<a href="#" class="alert-link margin-left-15" onclick="Login();"></a>');
				break;
			}
			case -40 : {// 
				require('./message').danger(response.message);
				break;
			}
			case -41 : {// CSRF
				require('./message').danger(response.message);
				break;
			}
			default : {
				try {
					dfd.resolve(response);
				} catch (e) {
					console.error(e);
				}
				break;
			}
		}
	};
	this.options.error = function(jqXHR, textStatus, errorThrown) {
		$loadingContainer.danger(uuid);
		switch (textStatus) {
			case null :
				// 
				errorThrown = (errorThrown == '')
						? 'jQuery.ajaxnull'
						: errorThrown;
				require('./message').danger(':' + errorThrown);
				break;
			case "timeout" :
				require('./message').danger('');
				break;
			case "error" :
				// 'Not Found'
				// 'Internal Server Error'Controllercatch
				// Throwable
				if (errorThrown === 'Not Found'
						|| errorThrown === 'Internal Server Error') {
					// NO-OP
				} else if (jqXHR.readyState === 0) {
					require('./message')
							.danger(
									'1.,2.,3.,4.');
				} else {
					errorThrown = (errorThrown == '')
							? 'sorry, jQuery'
							: errorThrown;
					require('./message').danger(':' + errorThrown);
				}
				break;
			case "abort" :
				// NO-OP
				require('./message').warning('');
				break;
			case "parsererror" :
				require('./message').danger('');
				break;
		}
		dfd.reject();
	};
	this.start = function() {
		if (AJAX_PROXY.ajaxMap[obj.key]) {
			require('./message').info('');
			return;
		} else {
			AJAX_PROXY.ajaxMap[obj.key] = obj;
		}
		$.ajax(obj.options);
	};
	this.start();
	return dfd.promise();
};
$.ajaxSetup({
	beforeSend : function(jqXHR, settings) {
		var session = sessionStorage.currentSession;
		if (!!session) {
			session = JSON.parse(session);
			var CsrfToken = session["CSRF_TOKEN"];
			if (!!CsrfToken) {
				jqXHR.setRequestHeader(CsrfToken.headerName, CsrfToken.token);
			}
			var user = session["CUR_USER"];
			if (!!user) {
				jqXHR.setRequestHeader('userID', user.id);
				jqXHR.setRequestHeader('userName',
						encodeURIComponent(user.name));
				jqXHR.setRequestHeader('userAccount',
						encodeURIComponent(user.account));
			}
			jqXHR.setRequestHeader('Accept', 'application/json');

		}
		return true;
	},
	statusCode : {
		// statusCode
		// 100
		// 101HTTP
		//
		// 200
		// 201URL
		//
		// 300
		// 301
		//
		// 404URl
		// 500
		403 : function() {
			require('./message').danger('');
		},
		404 : function() {
			require('./message').danger('');
		},
		500 : function() {
			require('./message').danger('');
		}
	}

});


